/* eslint-disable */
import ng from 'vendor-bb-angular';

import bbStorageModuleKey, { bbStorageServiceKey } from 'lib-bb-storage-ng';
import batchesData from './data-bb-batches-http';

const batchesDataModuleKey = 'data-bb-batches-http-ng';

export const batchesDataKey = 'data-bb-batches-http-ng:batchesData';

export default ng
  .module(batchesDataModuleKey, [
    bbStorageModuleKey,
  ])
  .provider(batchesDataKey, [() => {
    const config = {
      baseUri: '/',
    };

    return {
      setBaseUri: (baseUri) => {
        config.baseUri = baseUri;
      },
      $get: [
        '$q',
        bbStorageServiceKey,
        '$httpParamSerializer',
        /* into */
       batchesData(config),
      ],
    };
  }])
  .name;



